;/**
 * Description:
 *     first letter to uppercase
 *     second letter to lowercase
 * Usage:
 *   {{some_text | breakingbad}}
 *
 */

 angular.module("aliga.filters.breakingbad", []).filter("breakingbad", function() {
  "use strict";
  return function(input) {
    input = input.split(" e ").join("/");
    return (!!input) ? input.replace(/([^\W_]+[^\s\/]*) */g, function(txt) {
      return txt.charAt(0).toUpperCase() + txt.charAt(1).toLowerCase();}) : "";
  };
});

;/**
 * Description:
 *     first letter to uppercase
 * Usage:
 *   {{some_text | capitalize}}
 * From:
 *   http://codepen.io/WinterJoey/pen/sfFaK
 */

 angular.module("aliga.filters.capitalize", []).filter("capitalize", function() {
  "use strict";
  return function(input) {
    return (!!input) ? input.replace(/([^\W_]+[^\s-]*) */g, function(txt) {
      return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();}) : "";
  };
});

;/**
 * Description:
 *     from integer to proper date object suitable for filtering
 *     works only for months
 * Usage:
 *   {{ 9 | integer2date | date: 'MMMM' }}
 * From:
 *   http://stackoverflow.com/a/21482265
 */

 angular.module("aliga.filters.integer2date", []).filter("integer2date", function() {
  "use strict";
  return function(iMonth) {
    var convertme = iMonth-1;
    return new Date(2014, convertme);
  };
});

;/**
 * Description:
 *     removes white space from text. useful for html values that cannot have spaces
 * Usage:
 *   {{some text | nospace}}
 * From:
 *   https://gist.github.com/builtbylane/7237798
 */

angular.module("aliga.filters.nospace", []).filter("nospace", function () {
    "use strict";
    return function (value) {
        return (!value) ? "" : value.replace(/ /g, "");
    };
});
